




import Vue from "vue";

export default Vue.extend({
  name: "UiBadge",
  props: {
    variant: {
      type: String,
      required: true,
      validator(val: string): boolean {
        return ["contained", "outlined"].includes(val);
      }
    },
    color: {
      type: String,
      required: true,
      validator(val: string): boolean {
        return ["black", "blue", "green"].includes(val);
      }
    }
  }
});
