










































































import Vue from "vue";
import UiGrid from "../grid/index.vue";
import UiButton from "../button/index.vue";
import UiDropdown from "../dropdown/index.vue";
import UiMenuItem from "../menu/item.vue";
import ArrowIcon from "../../assets/right-arrow.svg";
import MoreIcon from "../../assets/more.svg";

export default Vue.extend({
  name: "UiBreadcrumbs",
  components: {
    UiGrid,
    UiButton,
    UiDropdown,
    UiMenuItem,
    ArrowIcon,
    MoreIcon
  },
  props: {
    items: {
      type: Array,
      required: true
    },
    uniqueKey: {
      type: String,
      required: false,
      default: "uuid"
    },
    labelKey: {
      type: String,
      required: false,
      default: "name"
    }
  },
  computed: {
    visibleItems(): any {
      if (this.short) {
        return [
          this.items[0],
          { isDropdown: true },
          this.items[this.items.length - 2],
          this.items[this.items.length - 1]
        ];
      }

      return this.items;
    },
    hiddenItems(): any {
      if (this.short) {
        return this.items.slice(1, this.items.length - 2);
      }

      return [];
    },
    short(): boolean {
      return this.items.length > 4;
    },
    lastItem(): any {
      return this.items.length ? this.items[this.items.length - 1] : null;
    }
  },
  methods: {
    handleItemClick(e: MouseEvent, item: any, click: any) {
      if (item.isDropdown) return;

      click(e);

      this.$emit("item-click", item, e);
    }
  }
});
